/**
 * 一些常用功能
 * @author Radish
 */

import NativeResponse from "../native/NativeResponse";

export default class Utils {
  /**
   * 对 parent 节点截屏
   * @param parent 截屏节点
   * @param filePath 图片保存路径
   * @author Radish
   */
  public static captureScreen(parent: cc.Node, filePath: string) {
    let node = new cc.Node();
    node.parent = parent;
    node.width = parent.getContentSize().width;
    node.height = parent.getContentSize().height;
    let camera = node.addComponent(cc.Camera);

    // 新建一个 RenderTexture，并且设置 camera 的 targetTexture 为新建的 RenderTexture，这样 camera 的内容将会渲染到新建的 RenderTexture 中。
    let texture = new cc.RenderTexture();
    texture.initWithSize(node.width, node.height);
    camera.targetTexture = texture;

    // 渲染一次摄像机，即更新一次内容到 RenderTexture 中
    parent.scaleY = -1;
    camera.render(node.parent);
    parent.scaleY = 1;

    // 这样我们就能从 RenderTexture 中获取到数据了
    let data = texture.readPixels();
    let width = texture.width;
    let height = texture.height;
    let fullPath = filePath;
    if (jsb.fileUtils.isFileExist(fullPath)) {
      jsb.fileUtils.removeFile(fullPath);
    }

    let result = jsb.saveImageData(data, width, height, fullPath);
    node.destroy();

    return result;
  }

  /**
   * 显示 iOS 弹窗
   * @author Radish
   */
  public static showAlert(
    title: string,
    message: string,
    buttonTitles: string[],
    callback?: (index: number) => void
  ) {
    let buttons = [];
    buttonTitles.forEach(value => {
      buttons.push({
        title: value,
        style: 0
      });
    });

    jsb.reflection.callStaticMethod(
      "QXQUtils",
      "showAlert:",
      JSON.stringify({
        title,
        message,
        buttons,
        callback: NativeResponse.push((value: string) => {
          if (callback) {
            let data: any = JSON.parse(value);
            callback(data.idx);
          }
        })
      })
    );
  }
}
