const { ccclass, property } = cc._decorator;

import AnalyticsUtils from "../native/AnalyticsUtils";
import Localized from "../utils/i18n/Localized";
import NativeResponse from "../native/NativeResponse";
import App from "../App";
import Utils from "../utils/Utils";

const PRODUCT_IDENTIFIERS = [
  "com.qxqplant.answers.water",
  "com.qxqplant.answers.coffee",
  "com.qxqplant.answers.noodles"
];

interface SubscriptionPeriod {
  numberOfUnits?: number;
  unit?: number;
}

interface IntroductoryPrice {
  price?: string;
  currencySymbol?: string;
  currencyCode?: string;
  subscriptionPeriod?: SubscriptionPeriod;
  numberOfPeriods?: number;
  paymentMode?: number;
}

interface Product {
  localizedDescription?: string;
  localizedTitle?: string;
  price?: string;
  currencySymbol?: string;
  currencyCode?: string;
  productIdentifier?: string;
  downloadable?: boolean;
  downloadContentLengths?: number[];
  downloadContentVersion?: string;
  subscriptionPeriod?: SubscriptionPeriod;
  introductoryPrice?: IntroductoryPrice;
  subscriptionGroupIdentifier?: string;
}

@ccclass
export default class PurchaseInfo extends cc.Component {
  onLoad() {
    AnalyticsUtils.trackEvent("PurchaseInfoPage");

    Localized.localizeLabel(this.labelTitle);
    Localized.localizeLabel(this.labelClose);

    this.m_buttons = [
      this.btnDonateWater,
      this.btnDonateCoffee,
      this.btnDonateNoodles
    ];
    this.initButtons(this.m_buttons);

    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      App.getInstance().active = true;

      jsb.reflection.callStaticMethod(
        "ApplePayUtils",
        "init:",
        JSON.stringify({
          callback: NativeResponse.push((value: string) => {
            let data = JSON.parse(value);
            if (data.action === "updated") {
              if (data.state === "purchased") {
                App.getInstance().active = false;
                let product = this.findProduct(data.payment.productIdentifier);
                if (product) {
                  Utils.showAlert(Localized.getValue("#purchase_success_title"), product.localizedDescription, [Localized.getValue("#btn_confirm")]);
                } else {
                  Utils.showAlert(Localized.getValue("#purchase_success_title"), "", [Localized.getValue("#btn_confirm")]);
                }
              } else if (data.state === "failed") {
                App.getInstance().active = false;
                Utils.showAlert(Localized.getValue("#purchase_failed_title"), data.err, [Localized.getValue("#btn_confirm")]);
              }
            }
          })
        })
      );

      jsb.reflection.callStaticMethod(
        "ApplePayUtils",
        "requestProducts:",
        JSON.stringify({
          identifiers: PRODUCT_IDENTIFIERS,
          callback: NativeResponse.push((value: string) => {
            App.getInstance().active = false;

            let data: {
              invalidProductIdentifiers?: string[];
              products: Product[];
            } = JSON.parse(value);

            if (data.products && data.products.length >= 3) {
              this.m_products = data.products;

              for (let p of data.products) {
                for (let index in PRODUCT_IDENTIFIERS) {
                  let identifier = PRODUCT_IDENTIFIERS[index];
                  if (p.productIdentifier === identifier) {
                    if (this.m_buttons[index]) {
                      this.initButton(this.m_buttons[index], p);
                    }
                    break;
                  }
                }
              }
            } else {
              Utils.showAlert(
                Localized.getValue("#request_products_failed_title"),
                Localized.getValue("#request_products_failed_content"),
                [Localized.getValue("#btn_confirm")]
              );
              this.node.destroy();
            }
          })
        })
      );
    }
  }

  onDestroy() {}

  /**
   * 点击捐赠按钮
   * @author Radish
   */
  onClickDonate(eventTouch: cc.Event.EventTouch, data: string) {
    let index = parseInt(data);
    let identifier = PRODUCT_IDENTIFIERS[index];
    if (identifier && this.findProduct(identifier)) {
      jsb.reflection.callStaticMethod(
        "ApplePayUtils",
        "payment:",
        JSON.stringify({ identifier })
      );

      App.getInstance().active = true;
    }
  }

  /**
   * 点击关闭按钮
   * @author Radish
   */
  onClickClose() {
    this.node.destroy();
  }

  /**
   * 初始化按钮
   * @author Radish
   */
  private initButton(button: cc.Node, product?: Product) {
    let label = button.getChildByName("Label").getComponent(cc.Label);

    if (!product) {
      label.string = Localized.getValue("#loading");
    } else {
      let dot = "";
      if (product.price.indexOf(".") === -1) {
        dot = ".00";
      }

      label.string = `${product.currencySymbol}${product.price}${dot} ${
        product.localizedTitle
      }`;
    }
  }

  /**
   * 初始化按钮显示
   * @author Radish
   */
  private initButtons(buttons: cc.Node[]) {
    for (let b of buttons) {
      this.initButton(b);
    }
  }

  /**
   * 查询 Product 对象
   * @author Radish
   */
  private findProduct(identifier: string) {
    for (let p of this.m_products) {
      if (p.productIdentifier === identifier) {
        return p;
      }
    }

    return null;
  }

  @property(cc.Label)
  labelTitle: cc.Label = null;

  @property(cc.Label)
  labelClose: cc.Label = null;

  @property(cc.Node)
  btnDonateWater: cc.Node = null;

  @property(cc.Node)
  btnDonateCoffee: cc.Node = null;

  @property(cc.Node)
  btnDonateNoodles: cc.Node = null;

  private m_buttons: cc.Node[] = [];
  private m_products: Product[] = [];
}
