/**
 * 顶层屏蔽触摸节点
 * @author Radish
 */

const { ccclass, property } = cc._decorator;

@ccclass
export default class ActivityNode extends cc.Component {
  onEnable() {
    cc.log("=> ActivityNode Enabled");
    this.icon.stopAllActions();
    this.icon.runAction(cc.repeatForever(cc.rotateBy(0.3, -90)));
  }

  onDisable() {
    cc.log("=> ActivityNode Disable");
    this.icon.stopAllActions();
  }

  @property(cc.Node)
  icon: cc.Node = null;
}

