/**
 * 当前应用实例, 单例
 * @author Radish
 */

import AnswersDictionary from "./AnswersDictionary";
import Constants from "./AnswersConstants";
import Localized from "./utils/i18n/Localized";

let s_app: App = null;

export default class App {
  /**
   * 获得唯一的 App 实例
   * @author Radish
   */
  public static getInstance(): App {
    if (s_app === null) {
      s_app = new App();
    }
    return s_app;
  }

  /**
   * 获得 AnswersDictionary 对象
   * @author Radish
   */
  public get answersDictionary(): AnswersDictionary {
    return this.m_answersDictionary;
  }

  /**
   * 初始化
   * @author Radish
   */
  public async init() {
    if (this.m_isInit) {
      return;
    }

    this.m_isInit = true;

    let promise = new Promise(async (resolve, reject) => {
      let language = await Localized.getLanguage();
      cc.loader.loadRes("configs/answers-" + language, (err, file) => {
        if (err) {
          reject(err);
        } else {
          this.m_answersDictionary = new AnswersDictionary(file.text);

          this.m_showCount = cc.sys.localStorage.getItem(
            Constants.STORAGE_KEYS.SHOW_COUNT
          );
          if (!this.m_showCount) {
            this.m_showCount = 0;
          }

          this.m_isEvaluate = cc.sys.localStorage.getItem(
            Constants.STORAGE_KEYS.IS_EVALUATE
          );
          if (!this.m_isEvaluate) {
            this.m_isEvaluate = false;
          } else {
            let time = cc.sys.localStorage.getItem(
              Constants.STORAGE_KEYS.EVALUATE_TIME
            );
            if (time) {
              this.m_evaluateTime = parseInt(time);
            }
          }

          resolve();
        }
      });
    });

    return promise;
  }

  public get showCount() {
    return this.m_showCount;
  }

  public set showCount(value) {
    this.m_showCount = value;
    cc.sys.localStorage.setItem(
      Constants.STORAGE_KEYS.SHOW_COUNT,
      this.m_showCount
    );
  }

  public get isEvaluate() {
    return this.m_isEvaluate;
  }

  public set isEvaluate(value) {
    this.m_isEvaluate = value;
    cc.sys.localStorage.setItem(
      Constants.STORAGE_KEYS.IS_EVALUATE,
      this.m_isEvaluate
    );

    if (this.m_isEvaluate) {
      this.m_evaluateTime = cc.sys.now();
      cc.sys.localStorage.setItem(
        Constants.STORAGE_KEYS.EVALUATE_TIME,
        this.m_evaluateTime
      );
    } else {
      this.m_evaluateTime = null;
      cc.sys.localStorage.removeItem(Constants.STORAGE_KEYS.EVALUATE_TIME);
    }

    cc.director.emit(Constants.EVENT.CHANGE_IS_EVALUATE);
  }

  /**
   * 判断是否展示评分
   * @returns true 展示, false 不展示
   * @author Radish
   */
  public get isShowEvaluate() {
    if (!this.isEvaluate) {
      if (this.showCount >= Constants.EVALUATE.SHOW_COUNT_MAX) {
        return true;
      }
    } else {
      if (this.m_evaluateTime) {
        let now = cc.sys.now();
        if (now - this.m_evaluateTime >= Constants.EVALUATE.EVALUATE_TIME_MAX) {
          return true;
        }
      }
    }

    return false;
  }

  /**
   * 触摸屏蔽层控制
   * @author Radish
   */
  public set active(value: boolean) {
    this.activityNode.active = value;
  }

  private constructor() {}
  private m_isInit: boolean = false;
  private m_answersDictionary: AnswersDictionary = null;
  private m_showCount = 0;
  private m_isEvaluate = false;
  private m_evaluateTime = null;

  public mainNode: cc.Node = null;
  public bottomNode: cc.Node = null;
  public middleNode: cc.Node = null;
  public topNode: cc.Node = null;
  public activityNode: cc.Node = null;
}
