export default class LocalizedDictionary {
  /**
   * 获得 LocalizedDictionary 实例
   * @author Radish
   */
  public static getInstance(): LocalizedDictionary {
    if (!this.s_instance) {
      this.s_instance = new LocalizedDictionary();
    }
    return this.s_instance;
  }

  private constructor() {
    let language = cc.sys.language;
    
  }

  private static s_instance = null;
}
