const BLACK = cc.color(128, 128, 128);
const WHITE = cc.Color.WHITE;

export default class ButtonUtils {
  /**
   * 给 button 添加点击着色处理
   * @author Radish
   */
  public static onColor(button: cc.Button, childName?: string) {
    if (childName) {
      let child = button.node.getChildByName(childName);

      button.node.on(cc.Node.EventType.TOUCH_START, () => {
        if (child) {
          child.color = BLACK;
        }
      });

      button.node.on(cc.Node.EventType.TOUCH_END, () => {
        if (child) {
          child.color = WHITE;
        }
      });
    } else {
      button.node.on(cc.Node.EventType.TOUCH_START, () => {
        for (let c of button.node.children) {
          c.color = BLACK;
        }
      });

      button.node.on(cc.Node.EventType.TOUCH_END, () => {
        for (let c of button.node.children) {
          c.color = WHITE;
        }
      });
    }
  }

  /**
   * 取消 button 之前添加的着色处理
   * @author Radish
   */
  public static offColor(button: cc.Button) {
    button.node.off(cc.Node.EventType.TOUCH_START);
    button.node.off(cc.Node.EventType.TOUCH_END);
  }
}
