/**
 * 答案字典, 管理答案数据
 * @author Radish
 */

const SPLIT_CHARACTERS_1 = ";\n";
const SPLIT_CHARACTERS_2 = ";\r\n";

export default class AnswersDictionary {
  constructor(rawdata: string) {
    this.m_arrayText = this.splitRawdata(rawdata);
    for (let t of this.m_arrayText) {
      this.m_dictText[t] = true;
    }
  }

  /**
   * 随机挑选出使用的文本
   * @author Radish
   */
  public randomText() {
    let index = parseInt((this.m_arrayText.length * Math.random()).toString());
    cc.log(`=> index = ${index}`);

    let text = this.m_arrayText[index];
    cc.log(`=> text = ${text}`);

    return text.replace("|", "\n");
  }

  /**
   * 补充答案内容
   * @author Radish
   */
  public patch(rawdata: string) {
    for (let t of this.splitRawdata(rawdata)) {
      if (!this.m_dictText[t]) {
        this.m_arrayText.push(t);
        this.m_dictText[t] = true;
      }
    }
  }

  /**
   * 解析文本数据
   * @author Radish
   */
  private splitRawdata(rawdata: string) {
    let texts = rawdata.split(SPLIT_CHARACTERS_1);
    if (texts.length === 1) {
      texts = rawdata.split(SPLIT_CHARACTERS_2);
    }
    return texts;
  }

  private m_arrayText: string[] = [];
  private m_dictText: { [k: string]: boolean } = {};
}
