/**
 * 测试统计分析接口
 * @author Radish
 */

const { ccclass } = cc._decorator;

import AnalyticsUtils from "../native/AnalyticsUtils";

@ccclass
export default class TestAnalyticsUtils extends cc.Component {
  onClickPageAppear() {
    AnalyticsUtils.trackPageAppear("testPage");
  }

  onClickPageDisappear() {
    AnalyticsUtils.trackPageDisappear("testPage");
  }

  onClickTrackEvent() {
    AnalyticsUtils.trackEvent("testEvent");
  }

  onClickTrackEventWithProperties() {
    AnalyticsUtils.trackEvent("testEvent", { key1: "value1", key2: "value2" });
  }

  onClickTrackEventDurationBegin() {
    AnalyticsUtils.trackEventDurationBegin("testEvent");
  }

  onClickTrackEventDurationEnd() {
    AnalyticsUtils.trackEventDurationEnd("testEvent");
  }

  onClickTrackEventDuration() {
    AnalyticsUtils.trackEventWithDuration("testEvent", 10);
  }
}
