const { ccclass, property } = cc._decorator;

@ccclass
export default class EvaluateAnimation extends cc.Component {
  onLoad() {
    const angle = 8;
    this.node.runAction(cc.sequence([cc.rotateBy(0.3, -angle).easing(cc.easeSineOut()), cc.callFunc(() => {
      let actions: cc.ActionInterval[] = [];
      actions.push(cc.rotateBy(0.8, angle * 2).easing(cc.easeSineInOut()));
      actions.push(cc.rotateBy(0.8, -angle * 2).easing(cc.easeSineInOut()));
      this.node.runAction(cc.repeatForever(cc.sequence(actions)));
    })]));
  }
}
