/**
 * i18n 处理
 * @author Radish
 */

import NativeResponse from "../../native/NativeResponse";

export default class Localized {
  /**
   * 获得当前使用语言标识
   * @author Radish
   */
  public static async getLanguage() {
    if (!this.s_language) {
      if (cc.sys.isBrowser) {
        this.s_language = this.process(window.navigator.language);
        return Promise.resolve(this.s_language);
      }
      if (cc.sys.os === cc.sys.OS_IOS) {
        return new Promise((resolve, reject) => {
          jsb.reflection.callStaticMethod(
            "QXQUtils",
            "getLanguage:",
            JSON.stringify({
              callback: NativeResponse.push((value: string) => {
                this.s_language = this.process(value);
                resolve(this.s_language);
              })
            })
          );
        });
      } else {
        this.s_language = "en";
        return Promise.resolve(this.s_language);
      }
    } else {
      return this.s_language;
    }
  }

  /**
   * 初始化
   * @author Radish
   */
  public static async init() {
    let language = await this.getLanguage();

    let p1 = new Promise(async (resolve, reject) => {
      cc.loader.loadRes("configs/localized-" + language, (err, file) => {
        if (!err) {
          this.s_dict = file.json;
          resolve();
        } else {
          reject();
        }
      });
    });

    let p2 = new Promise((resolve, reject) => {
      cc.loader.loadRes("configs/localized-en", (err, file) => {
        if (!err) {
          this.s_defaultDict = file.json;
          resolve();
        } else {
          reject();
        }
      });
    });
    return p1.then(() => p2).catch(() => p2);
  }

  /**
   * 从本地化字典获得对应的值
   * @author Radish
   */
  public static getValue(key: string) {
    let value = this.s_dict[key];
    if (!value) {
      value = this.s_defaultDict[key];
      if (!value) {
        return key;
      }
    }
    return value;
  }

  /**
   * 本地化 label 控件
   * @author Radish
   */
  public static localizeLabel(label: cc.Label) {
    label.string = this.getValue(label.string);
  }

  /**
   * 对浏览器/终端返回的语言字符串做处理
   * @author Radish
   */
  private static process(value: string): string {
    value = value.toLocaleLowerCase();
    cc.log(`=> process value = ${value}`);

    if (value.indexOf("zh-hant") !== -1) {
      return "zh-hant";
    } else if (value.indexOf("zh-hans") !== -1) {
      return "zh-hans";
    } else if (value.indexOf("zh-tw") !== -1 || value.indexOf("zh-hk") !== -1) {
      return "zh-hant";
    } else if (value.indexOf("zh-cn") !== -1) {
      return "zh-hans";
    } else if (value === "zh") {
      return "zh-hans";
    } else {
      return "en";
    }
  }

  private static s_language: string = null;
  private static s_dict: { [k: string]: string } = {};
  private static s_defaultDict: { [k: string]: string } = {};
}
