const { ccclass, property } = cc._decorator;

import App from "./App";
import ButtonUtils from "./utils/ButtonUtils";
import Utils from "./utils/Utils";
import NativeResponse from "./native/NativeResponse";
import Constants from "./AnswersConstants";
import AnalyticsUtils from "./native/AnalyticsUtils";
import Localized from "./utils/i18n/Localized";

@ccclass
export default class MainNode extends cc.Component {
  async onLoad() {
    if (cc.sys.isNative) {
      // https://www.tslang.cn/docs/handbook/functions.html
      let rawcclog = cc.log;
      cc.log = (msg: any, ...rest: any[]) => {
        rawcclog(msg, ...rest);
        console.log(msg, ...rest);
      };

      jsb.onHide = () => {
        cc.log("=> onHide");
      };

      jsb.onShow = () => {
        cc.log("=> onShow");
        this.showAnswer();
      };
    } else {
      this.btnShare.active = false;
    }

    cc.log("=> Localized start init");
    await Localized.init();
    cc.log("=> Localized finish init");
    cc.log(`=> current language = ${await Localized.getLanguage()}`);

    cc.log("=> App start init");
    await App.getInstance().init();
    cc.log("=> App finish init");

    let app: App = App.getInstance();
    app.mainNode = this.node;
    app.bottomNode = this.node.getChildByName("bottomNode");
    app.middleNode = this.node.getChildByName("middleNode");
    app.topNode = this.node.getChildByName("topNode");
    app.activityNode = this.activityNode;

    ButtonUtils.onColor(this.btnInfo, "icon");
    ButtonUtils.onColor(this.btnShare.getComponent(cc.Button), "icon");
    this.onChangeIsEvaluate = this.onChangeIsEvaluate.bind(this);
    cc.director.on(Constants.EVENT.CHANGE_IS_EVALUATE, this.onChangeIsEvaluate);

    this.showAnswer();
  }

  onDestroy() {
    cc.director.off(
      Constants.EVENT.CHANGE_IS_EVALUATE,
      this.onChangeIsEvaluate
    );
    ButtonUtils.offColor(this.btnInfo);
    ButtonUtils.offColor(this.btnShare.getComponent(cc.Button));
  }

  /**
   * 显示答案
   * @author Radish
   */
  public showAnswer() {
    let text: string = App.getInstance().answersDictionary.randomText();
    this.labelAnswer.string = text;

    AnalyticsUtils.trackEvent("ShowAnswerPage");

    this.labelAnswer.node.stopAllActions();
    this.labelAnswer.node.opacity = 0;
    const duration1 = 4;
    const duration2 = 20;
    let actions: cc.FiniteTimeAction[] = [];
    actions.push(cc.fadeIn(duration1));
    actions.push(cc.delayTime(duration2));
    actions.push(cc.fadeOut(duration1));
    this.labelAnswer.node.runAction(cc.sequence(actions));

    ++App.getInstance().showCount;

    if (cc.sys.isNative) {
      this.btnEvaluate.active = App.getInstance().isShowEvaluate;
    }

    // 分享按钮显示控制
    if (cc.sys.isNative) {
      this.btnShare.stopAllActions();
      this.btnShare.active = true;
      this.btnShare.runAction(
        cc.sequence([
          cc.delayTime(duration1 * 2 + duration2),
          cc.callFunc(() => (this.btnShare.active = false))
        ])
      );
    }
  }

  /**
   * 点击信息按钮
   * @author Radish
   */
  public onClickInfo() {
    this.showDevelopInfo();
  }

  /**
   * 点击评分按钮
   * @author Radish
   */
  public onClickEvaluate() {
    cc.log("=> onClickEvaluate");

    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "QXQUtils",
        "showEvaluate:",
        JSON.stringify({
          appId: Constants.APP.APPLE_APP_ID,
          isUseAppStore: false,
          callback: NativeResponse.push((value: string) => {
            cc.log(`=> ${value}`);

            let data: any = JSON.parse(value);
            if (data.state === "finish") {
              App.getInstance().isEvaluate = true;
            }
          })
        })
      );
    } else {
      App.getInstance().isEvaluate = true;
    }
  }

  /**
   * 点击分享
   * @author Radish
   */
  public onClickShare() {
    AnalyticsUtils.trackEvent("ShareStart");

    let path =
      jsb.fileUtils.getWritablePath() + Constants.APP.CAPTURE_SCREEN_FILE_NAME;

    let opacity = this.labelAnswer.node.opacity;
    this.labelAnswer.node.opacity = 255;

    if (Utils.captureScreen(App.getInstance().bottomNode, path)) {
      cc.log("=> 截图成功");
      jsb.reflection.callStaticMethod(
        "AppleShare",
        "shareImage:",
        JSON.stringify({
          path,
          callback: NativeResponse.push((value: string) => {
            let data = JSON.parse(value);
            if (data.result) {
              AnalyticsUtils.trackEvent("ShareFinish");
            }
          })
        })
      );
    } else {
      AnalyticsUtils.trackEvent("ShareCaptureScreenFailed");
    }

    this.labelAnswer.node.opacity = opacity;
  }

  /**
   * 展示开发者信息界面
   * @author Radish
   */
  public showDevelopInfo() {
    const childName = "developerInfo";

    let child = App.getInstance().topNode.getChildByName(childName);
    if (child) {
      App.getInstance().topNode.removeChild(child);
    }

    cc.loader.loadRes(
      "prefabs/developerInfo",
      cc.Prefab,
      (error: Error, resounce: any) => {
        let node: cc.Node = cc.instantiate(resounce);
        node.y = -cc.winSize.height * 0.5;
        App.getInstance().topNode.addChild(node, 0, childName);
      }
    );
  }

  /**
   * 侦听投票属性改变事件
   * @author Radish
   */
  private onChangeIsEvaluate() {
    if (cc.sys.isNative) {
      this.btnEvaluate.active = App.getInstance().isShowEvaluate;
    }
  }

  @property(cc.Label)
  labelAnswer: cc.Label = null;

  @property(cc.Button)
  btnInfo: cc.Button = null;

  @property(cc.Node)
  btnEvaluate: cc.Node = null;

  @property(cc.Node)
  btnShare: cc.Node = null;

  @property(cc.Node)
  activityNode: cc.Node = null;
}
