const { ccclass, property } = cc._decorator;

import App from "../App";
import NativeResponse from "../native/NativeResponse";
import AnalyticsUtils from "../native/AnalyticsUtils";
import Utils from "../utils/Utils";
import Localized from "../utils/i18n/Localized";

@ccclass
export default class DeveloperInfo extends cc.Component {
  onLoad() {
    AnalyticsUtils.trackEvent("DevelopInfoPage");

    // donate 按钮动画
    let btnDonate: cc.Node = cc.find("bg/btnDonate", this.node);
    btnDonate.angle = 0;
    let actions: cc.ActionInstant[] = [];
    actions.push(cc.rotateTo(0.8, -18).easing(cc.easeOut(3.0)));
    actions.push(cc.rotateTo(0.45, 0).easing(cc.easeElasticOut(0.3)));
    actions.push(cc.delayTime(1.618));
    btnDonate.runAction(cc.repeatForever(cc.sequence(actions)));

    // 触摸其他区域关闭当前界面
    let isPressed = false;
    this.node.on(cc.Node.EventType.TOUCH_START, () => {
      isPressed = true;
    });

    this.node.on(cc.Node.EventType.TOUCH_END, () => {
      if (isPressed) {
        isPressed = false;
        this.hide();
      }
    });

    this.show();
  }

  onDestroy() {}

  /**
   * 展示
   * @author Radish
   */
  show() {
    let bg: cc.Node = this.node.getChildByName("bg");
    bg.stopAllActions();

    bg.y = -bg.getContentSize().height;
    bg.runAction(cc.moveBy(0.1, 0, bg.getContentSize().height));
  }

  /**
   * 隐藏
   * @author Radish
   */
  hide() {
    let bg: cc.Node = this.node.getChildByName("bg");
    bg.stopAllActions();

    bg.y = 0;
    bg.runAction(
      cc.sequence([
        cc.moveBy(0.1, 0, -bg.getContentSize().height),
        cc.callFunc(() => {
          this.node.destroy();
        })
      ])
    );
  }

  /**
   * 点击购买按钮
   * @author Radish
   */
  onClickPurchase() {
    cc.loader.loadRes(
      "prefabs/purchaseInfo",
      cc.Prefab,
      (err: Error, resource: any) => {
        if (!err) {
          let node: cc.Node = cc.instantiate(resource);
          App.getInstance().topNode.addChild(node);
        }
      }
    );
  }

  /**
   * 点击复制邮件
   * @author Radish
   */
  onClickCopyEmail() {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "QXQUtils",
        "copyText:",
        JSON.stringify({
          text: this.labelEmail.string,
          callback: NativeResponse.push(() => {
            Utils.showAlert(
              "",
              Localized.getValue("#copy_email_success_content"),
              [Localized.getValue("#btn_confirm")]
            );
          })
        })
      );
    }
  }

  /**
   * 点击复制网址
   * @author Radish
   */
  onClickCopyWebsite() {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "QXQUtils",
        "copyText:",
        JSON.stringify({
          text: this.labelWebsite.string,
          callback: NativeResponse.push(() => {
            Utils.showAlert(
              "",
              Localized.getValue("#copy_website_success_content"),
              [Localized.getValue("#btn_confirm")]
            );
          })
        })
      );
    }
  }

  /**
   * 点击去指定网站
   * @author Radish
   */
  onClickGoWebsite() {
    cc.sys.openURL(this.labelWebsite.string);
  }

  /**
   * 点击发送邮件
   * @author Radish
   */
  onClickSendEmail() {
    if (cc.sys.isNative && cc.sys.os === cc.sys.OS_IOS) {
      jsb.reflection.callStaticMethod(
        "QXQUtils",
        "sendEmail:",
        JSON.stringify({
          subject: "体验反馈",
          toRecipients: [this.labelEmail.string],
          content: "\n\n\n\n\n\n感谢您的建议",
          callback: NativeResponse.push((value: string) => {
            cc.log(`=> ${value}`);
          })
        })
      );
    }
  }

  @property(cc.Label)
  labelWebsite: cc.Label = null;

  @property(cc.Label)
  labelEmail: cc.Label = null;
}
